import React, { Fragment, createElement, useEffect, useState } from 'react'
import * as prod from 'react/jsx-runtime'
import rehypeParse from 'rehype-parse'
import rehypeReact from 'rehype-react'
import { unified } from 'unified'
import ExpoSnackWrapper from '../ExpoSnack/exposnack';

import TextLink from "../base/links/text-link";
import { Text } from "../base/typography";
import { Heading } from "../base/typography";
import Image from "../base/image";


function useProcessor(html, options) {
    const [Content, setContent] = useState(createElement(Fragment))
    const production = {
        Fragment: prod.Fragment,
        jsx: prod.jsx,
        jsxs: prod.jsxs,
        components: {
            h2: ({ node, ...props }) => <Heading {...props} level={2} noMargin />,
            h3: ({ node, ...props }) => <Heading {...props} level={3} noMargin />,
            h4: ({ node, ...props }) => <Heading {...props} level={4} noMargin />,
            h5: ({ node, ...props }) => <Heading {...props} level={5} noMargin />,
            p: ({ node, ...props }) => <Text {...props} $noTopMargin={!options.p.topMargin} />,
            a: ({ node, href, ...props }) => <TextLink to={href} {...props} />,
            img: ({ node, ...props }) => <Image {...props} />,
            "expo-snack": ({ node, ...props }) => <ExpoSnackWrapper {...props} />
        }
    }

    useEffect(
        function () {
            ; (async function () {
                const file = await unified()
                    .use(rehypeParse, { fragment: true })
                    .use(rehypeReact, production)
                    .process(html)

                setContent(file.result)
            })()
        },
        [html]
    )

    return Content
}

const MarkdownContent = ({ html, options }) => {
    return useProcessor(html, options)
    // return <div></div>
}

MarkdownContent.defaultProps = {
    options: {
        p: {
            topMargin: true
        }
    }
}

export default MarkdownContent;