import React from 'react';
import styled from 'styled-components';
import ExpoSnack  from 'react-expo-snack'; 

const StyledExpoSnack = styled.div`
    width: 40rem;
    max-width: 100%;
`;

const ExpoSnackWrapper = ({ id }) => {
    console.log(id)
    return (
        <StyledExpoSnack>
            <ExpoSnack id={id}/>
        </StyledExpoSnack>
    )
};

export default ExpoSnackWrapper;